import React from 'react';

import SEO from '../components/SEO';
import ContactForm from '../components/ContactForm';

const ContactPage = () => {
  return (
    <div className="contain-sm mt-8 md:mt-16">
      <h1 className="text-3xl md:text-4xl md:text-5xl">Get In Touch</h1>
      <p className="text-mdgray my-4 md:text-lg">
        Have a project in mind or just want to talk? Feel free to send me a
        message!
      </p>
      <ContactForm />
    </div>
  );
};

export default ContactPage;
export const Head = () => (
  <SEO
    title="Contact"
    description="Have a project in mind or just want to talk? Feel free to send me a message!"
  />
);
